.nav-container {
    margin: 0 auto;
    padding: 10px;
}

.nav-logo {
    max-width: 50px;
    margin-right: 10px;
    vertical-align: middle;
}

.nav-container .hamburger {
    display: none;
  }

.nav-l-text {
    font-size: 2.5rem;
    color: black;
    vertical-align: middle;
    display: inline-block;
    font-weight: 700;
    font-family: "JuliusSansOne";
}

.nav-links {
    float: right;
    text-align: right;
    padding-right: 25px;
}

.nav-link {
    position: relative;
    display: inline-block;
    text-decoration: none !important;
    cursor: pointer;
    height: 50px;
    line-height: 50px;
    color: black !important;
    padding: 0px 10px 0px 10px;
    font-size: 18px;
    font-family: "LouisGeorgeCafe-Regular" !important;
  }

.nav-link:hover {
    color: #F2B725 !important;
}

.nav-select {
    color: #F2B725 !important;
}


/* Underline styling */
.nav-link::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 10px;
    left: 0;
    background-color: #F2B725;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
  }

.nav-link:hover::before {
    visibility: visible;
    -webkit-transform: scaleX(0.7);
            transform: scaleX(0.7);
}
.nav-select::before {
    content: "";
    position: absolute;
    height: 2px;
    bottom: 13px;
    left: 0;
    background-color: #F2B725;
    visibility: visible;
    -webkit-transform: scaleX(0.5);
            transform: scaleX(0.5);
    transition: all 0.3s ease-in-out 0s;
}

/* Mobile */
@media screen and (max-width: 1000px) {
    .nav-l-text {
        font-size: 1.8rem !important;
    }

    .ham {
        width: 38px;
    }
    
    .nav-logo {
        max-width: 35px;
    }

    .nav-links {
        padding-right: 0px;
    }

    .nav-link {
        font-size: 16px !important;
        height: 30px !important;
        line-height: 30px !important;
        padding: 0px 10px 0px 10px;
        margin: 5px 0 5px 0;
    }

    .nav-links {display: none;}
    .nav-links.responsive {position: relative;}
    
    .nav-links.responsive {
        display: block;
        float: none;
        margin-top: 20px;
    }
    
    .nav-links.responsive .nav-link {
        float: none;
        display: block;
        text-align: center;
    }
    .nav-container a.hamburger {
        float: right;
        display: block;
    }
    .nav-container.responsive .hamburger {
        position: absolute;
        right: 0;
        top: 0;
    }


    /* Underline styling */
    .nav-link::before {
        bottom: 0;
    }

    .nav-link:hover::before {
        -webkit-transform: scaleX(0.15);
                transform: scaleX(0.15);
    }
    .nav-select::before {
        bottom: 2px;
        -webkit-transform: scaleX(0.1);
                transform: scaleX(0.1);
    }
}

